import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'

import useJwt from '@src/auth/jwt/useJwt'

const ComplainDetails = () => {
    const { ComplainID } = useParams()
    const complain_id = String(ComplainID).split("-")[0]
    const item_id = String(ComplainID).split("-")[1]

    const getSingleComplainDetails = () => {
        useJwt.getSingleComplainDetails({ complain_id, item_id }).then(res => {
            console.log("getSingleComplainDetails", res.data.payload)
            // setComplainData(res.data.payload)
            // setTDL(false)
        }).catch(error => {
            console.log("getSingleComplainDetails", error)
            // setTDL(false)
            // Error(error)
        })
    }

    useEffect(() => {
        getSingleComplainDetails()
    }, [])
    return (
        <>
           
        </>
    )
}

export default ComplainDetails